// import { Toast } from "../../../node_modules/vant";
import { Toast } from 'vant';
/*判断是否全是数字*/
export function isAllNumber(v) {
  var reg = /^\d+$/;
  return reg.test(v);
}
/*校验手机号有效性*/
export function checkMobile(mobile,tip) {
  var regPhone = /^[1][3456789]\d{9}$/;
  if (!regPhone.test(mobile)) {
    Toast({
      message: `请输入正确的${tip}!` ,
    })
    return false;
  }
  return true;
}
//密码由6~20位大小写英文字母、阿拉伯数字或大小写英文字母和阿拉伯数字的组合
export function checkPass(code,tip) {
  var regCode=/^[A-Za-z0-9]{6,20}$/;
  if (!regCode.test(code)) {
    Toast({
      message: tip+'由6~20位大小写英文字母、阿拉伯数字或大小写英文字母和阿拉伯数字的组合',
    })
    return false;
  }
  return true;
}

/* 校验车架号有效性（17个字符，且由字母，数字和*号组成，第一位可以是汉字） */
export function checkFrameNo(frameNo) {
  var frameNoReg = /^[A-Za-z0-9]+$/;
  if (frameNo.length != 17 || !frameNoReg.test(frameNo)) {
    Toast({
      message: '车辆识别代码非空,17个字符，且由字母，数字组成',
    })
    return false;
  }
  return true;
}
/* 校验发动机号有效性（发动机号20个字符，不能含有中文） */
export function checkEngineNo(engineNo) {
  if (engineNo == null || engineNo.length == 0) {
    Toast({
      message: '发动机号格式不正确',
    })
    return false;
  }
  var engineNoReg = /^[A-Za-z0-9]+$/;
  if (!engineNoReg.test(engineNo)) {
    Toast({
      message: '发动机号格式不正确',
    })
    return false;
  }
  return true;
}
/* 校验车主名称有效性（2-15位中文或者4-30位英文字母） */
export function checkName(carOwner,tip) {
  var regName1 = /^[\u4e00-\u9fa5 ]{2,50}$/;
  var regName2 = /^[A-Za-z ]+$/;
  if (!regName1.test(carOwner) && !regName2.test(carOwner)) {
    Toast({
      message: tip+'姓名格式不正确',
    })
    return false;
  }
  return true;
}
/* 校验车牌号有效性（一个中文+5到10个数字字母）*/
export function checkLicenseNo(licenseNo) {
  var licenseNoUpperCase = licenseNo.toUpperCase();
  var regex = /^[\u4e00-\u9fa5]{1}[A-Z]{1}\w{5,6}$/;
  if (!regex.test(licenseNoUpperCase)) {
    Toast({
      message: '车牌格式不正确',
    })
    return false;
  }
  return true;
}
/* 验证邮箱格式是否正确*/
export function checkEmail(email,tip) {
  var regEmail = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
  if (!regEmail.test(email)) {
    Toast({
      message: tip+'电子邮件的格式不正确',
    })
    return false;
  }
  return true;
}