<template>
  <div class="login-company-main">
    <van-popup v-model="isShow" position="bottom" :overlay="true" :closeOnClickOverlay="false">
      <div class="company-title">
        <div class="close" @click="closeCompanyList">
          X
        </div>
        <div class="title">
          选择保险公司
        </div>
      </div>
      <ul class="login-company-content">
        <li class="login-company-item" @click="chooseCompany(index)" v-for="(item,index) in choosableCompanyCodes" :key="index">
          {{ item }}
        </li>
      </ul>
    </van-popup>
  </div>
</template>
<script>
export default {
  name: 'login-company',
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    choosableCompanyCodes: {
      type: Object,
      default: () => {}
    }
  },
  methods: {
    closeCompanyList() {
      this.$emit('closeCompanyList')
    },
    chooseCompany(index) {
      this.$emit('chooseCompany',index)
    }
  }
}
</script>
<style scoped lang="scss">
.van-popup--bottom{
  border-radius:10px 10px 0 0;
}
.login-company-main{
  width: 100%;
  background: $white;
  .company-title{
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    display: flex;
    background: $gray-dark;
    .close{
      position: absolute;
      left: 5%;
    }
    .title{
      flex: 1;
      font-size: 16px;
    }
  }
  .login-company-content{
    width: 100%;
    max-height: 252px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    .login-company-item{
      flex: 1;
      height: 50px;
      line-height: 50px;
      text-align: center;
      border-bottom: 1px solid $gray;
    }
  }
}
</style>
