<template>
  <div class="login-main">
    <c-nav-bar class="login-main-header">
      <span slot="right" class="nav-bar-right" @click="registered">
        注册
      </span>
    </c-nav-bar>
    <div class="login-content">
      <!--快捷登入-->
      <div class="quick-login" ref="quickLogin">
        <div class="login-title">
          <van-icon name="location"></van-icon>
          <span>快捷登录</span>
        </div>
        <ul class="turn-login-content">
          <li>
            <input type="text" v-model="quickLogin.mobile" placeholder="输入手机号">
          </li>
          <li class="get-verCode">
            <input type="text" :disabled="verDisabled" v-model="quickLogin.authCode" @keyup="toCommitLogin" placeholder="输入手机验证码">
            <c-countdown :isGoRegistered="isGoRegistered" :isMobileExists="isMobileExists" :getVerUrl="getVerUrl" :phoneCode="quickLogin.mobile" :totalTime="totalTime" @verCodeFlag="verCodeFlag"></c-countdown>
          </li>
        </ul>
        <div class="login-commit">
          <van-button round :disabled="loginBtnFlag" :class="[loginBtnFlag? 'bg-gray':'bg-orange-dark']" type="default" class="commit-login" @click="commitLogin('quickLogin')">
            确定登录
          </van-button>
          <div class="pass-commit" @click="turnLogin('pass')">
            密码登录
          </div>
        </div>
      </div>
      <!--密码登入-->
      <div class="pass-login" ref="passLogin">
        <div class="login-title">
          <van-icon name="location"></van-icon>
          <span>密码登录</span>
        </div>
        <ul class="turn-login-content">
          <li>
            <input type="text" v-model="passLogin.loginCode" placeholder="输入工号" @blur="passwordLoginPrecheck">
          </li>
          <li>
            <input type="password" v-model="passLogin.password" placeholder="输入密码">
          </li>
          <li v-show="idCardSuffixShow">
            <input type="text" maxlength="6" v-model="passLogin.idCardSuffix" placeholder="输入身份证后六位">
          </li>
          <li v-show="companyCodeShow" @click="openCompanyList">
            <input type="text" disabled v-model="companyName" placeholder="选择公司">
            <i class="iconfont icon-tiaozhuan" style="margin-left: 16%"></i>
          </li>
          <li class="other-pass">
            <van-checkbox v-model="checked">
              记住我
            </van-checkbox>
            <span class="forget-pass" @click="findPass">
              忘记密码？
            </span>
          </li>
          <login-company :isShow="isShow" :choosableCompanyCodes="choosableCompanyCodes" @closeCompanyList="closeCompanyList" @chooseCompany="chooseCompany"></login-company>
        </ul>
        <div class="login-commit">
          <van-button round type="default" :disabled="loginBtnFlag" :class="[loginBtnFlag? 'bg-gray':'bg-orange-dark']" class="commit-login" @click="commitLogin('passLogin')">
            确认登录
          </van-button>
          <div class="pass-commit" @click="turnLogin('quick')">
            快捷登录
          </div>
        </div>
      </div>
    </div>
    <div class="login-footer">
      <span>第三方登录</span>
      <i class="iconfont icon-weixin1"></i>
    </div>
  </div>
</template>
<script>
import loginCompany from '@/components/login/loginCompany'
import { checkMobile,isAllNumber } from '@/js/checkUtil'
export default {
  name: 'login',
  data() {
    return {
      companyName: '',
      isGoRegistered: true,//是否提示跳转到注册页面信息
      isMobileExists: true,//获取验证码时是否校验手机是否存在即判断是否首次登录
      loginCodeExists: false,//工号是否存在
      twiceIsMobileExists: false,//第二次调用是否显示身份证和公司选项的接口
      idCardSuffixShow: false,//是否显示身份证
      companyCodeShow: false,//是否显示公司
      isShow: false,//选择公司蒙层
      checked: false,//记住密码按钮
      loginBtnFlag: true,//确定登录按钮
      verDisabled: true,
      totalTime: 60,
      getVerUrl: '/api/authCode/sendForQuickLogin',
      quickLogin: {
        mobile: '',//手机号
        authCode: '',//验证码
      },
      passLogin: {
        loginCode: '',
        password: '',
      },
      choosableCompanyCodes: {}//存储选择公司选项
    }
  },
  components: {
    'login-company': loginCompany
  },
  watch: {
    //监听密码登录是否填写完成
    passLogin: {
      handler(){
        let flag;
        for ( let key in this.passLogin ) {
          if (!this.passLogin[key]){
            flag=true;
            break
          }
        }
        this.loginBtnFlag=flag||false;
      },
      deep: true
    }
  },
  methods: {
    //切换登录模式
    turnLogin(flag) {
      this.loginBtnFlag = true;
      //重置数据
      Object.assign(this.$data, this.$options.data())
      if (flag === 'pass') {
        this.$refs.quickLogin.style.left = '100%';
        this.$refs.passLogin.style.left = '0'
      } else {
        this.$refs.quickLogin.style.left = '0';
        this.$refs.passLogin.style.left = '100%'
      }
    },
    //获取验证码
    verCodeFlag(childValue) {
      this.verDisabled=childValue
    },
    //快捷登录时打开确定登录按钮
    toCommitLogin() {
      this.loginBtnFlag=this.quickLogin.authCode?false:true;
    },
    //密码登录校验是否显示身份证和公司
    passwordLoginPrecheck(flag) {
      if (!this.passLogin.loginCode) return;
      this.$http.post('/api/user/passwordLoginPrecheck',{
        loginCode: this.passLogin.loginCode,
        idCardSuffix: this.passLogin.idCardSuffix||''
      }).then(response => {
        this.loginCodeExists=response.data.data.loginCodeExists;//工号是否存在
        this.idCardSuffixShow=response.data.data.needIdcardSuffix;
        this.companyCodeShow=response.data.data.needCompanyCode;
        this.choosableCompanyCodes=response.data.data.choosableCompanyCodes;
        if (response.data.data.needIdcardSuffix && flag !== 'add') {
          this.$set(this.passLogin,'idCardSuffix','')
        }
        if (response.data.data.needCompanyCode && flag !== 'add') {
          this.$set(this.passLogin,'companyCode','')
        }
      }, response => {
        console.log(response)
      });
    },
    //选择公司打款蒙层
    openCompanyList() {
      this.isShow = true;
    },
    closeCompanyList() {
      this.isShow = false;
    },
    //选择公司
    chooseCompany(index) {
      this.companyName=this.choosableCompanyCodes[index];
      this.passLogin.companyCode=index;
      this.isShow = false;
    },
    //登录
    commitLogin(flag) {
      let url,data;
      if (flag==='quickLogin'){//快捷登录
        if (!checkMobile(this.quickLogin.mobile)) return
        if (!isAllNumber(this.quickLogin.authCode)) {
          this.$toast('请输入正确的验证码!');
          return
        }
        url='/api/user/quickLogin';
        data=this.quickLogin
      } else if (flag==='passLogin'){//密码登录状态
        if (!this.loginCodeExists) {
          this.$toast('请输入正确的工号!');
          return
        }
        //如果选择公司没有的话就再次检验身份证号是否有重复
        if (this.idCardSuffixShow && !this.twiceIsMobileExists) {
          this.twiceIsMobileExists=true;
          this.passwordLoginPrecheck('add');
        }
        url='/api/user/passwordLogin';
        data=this.passLogin
      }
      this.$http.post(url,data).then(response => {
        if (response.data.success) {
          this.$router.push({name: '首页'});
          this.$store.commit('set_userInfo',response.data.data);
          console.log(this.$store.state)
        } else {
          this.$toast(response.data.errorMessage)
        }
      }, response => {
        console.log(response)
      });
    },
    //跳转注册页面
    registered() {
      this.$router.push({name: '注册'})
    },
    findPass() {
      this.$router.push({name: '找回密码'})
    }
  }
}
</script>
<style scoped lang="scss">
.icon-weixin1{
  font-size: 60px;
  color: $green;
}
.bg-orange-dark{
  background:linear-gradient(left bottom,$orange-dark,$orange)
}
.bg-gray{
  background: $gray;
}
.login-main{
  .login-main-header{
    background: #fff;
  }
  .login-content{
    width:100%;
    position:absolute;
    bottom:150px;
    background: $white;
    display: flex;
    right: 0;
    overflow: hidden;
    top: 44px;
    .quick-login{
      width: 100%;
      padding:8%;
      position: absolute;
      left: 0;
    }
    .pass-login{
      width: 100%;
      height: 100px;
      position: absolute;
      left: 100%;
      padding:4%;
    }
    .login-title{
      height: 100px;
      line-height: 100px;
      font-size: 25px;
      color: $orange-dark;
      span{
        font-size: 25px;
        font-weight: bold;
        color: #000;
        margin-left: 2%;
      }
    }
    .login-commit{
      width: 100%;
      margin-top: 40px;
      .commit-login{
        width: 100%;
        color: $white;
      }
      .pass-commit{
        width: 100%;
        line-height: 40px;
        text-align: center;
        font-size: 15px;
      }
    }
    .turn-login-content{
      li{
        width: 100%;
        height: 50px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid $gray;
        input{
          width: 78%;
          height: 20px;
        }
      }
      .get-verCode{
        display: flex;
        align-items: center;
        input{
          flex: 1;
        }
      }
      .other-pass{
        display: flex;
        border-bottom: 0!important;
        div{
          flex: 1;
        }
        .forget-pass{
          color: $orange-dark;
        }
      }
    }
  }
  .login-footer{
    width: 100%;
    padding: 0 4%;
    height: 150px;
    position:absolute;
    bottom:0;
    background: $white;
    text-align: center;
    display: flex;
    flex-direction: column;
    span:before{
      content: '';
      display: inline-block;
      width: 30%;
      height: 1px;
      background: $gray;
      vertical-align: middle;
      margin: 0 4%;
    }
    span:after{
      content: '';
      display: inline-block;
      width: 30%;
      height: 1px;
      background: $gray;
      vertical-align: middle;
      margin: 0 4%;
    }
    .weChat-img{
      width: 60px;
      height: 60px;
      margin: 10px auto;
    }
  }
}
</style>
